/*******************************
            Header
*******************************/
/* Standard */
.ui.header {
  border: none;
  margin: calc(2rem -  0.14285em ) 0em 1rem;
  padding: 0em 0em;
  font-family: 'Lato', 'Helvetica Neue', Meiryo, Arial, Helvetica, sans-serif;
  font-weight: bold;
  line-height: 1.2857em;
  text-transform: none;
  color: rgba(0, 0, 0, 0.87);
}
.ui.header:first-child {
  margin-top: -0.14285em;
}
.ui.header:last-child {
  margin-bottom: 0em;
}
/*--------------
   Sub Header
---------------*/
.ui.header .sub.header {
  display: block;
  font-weight: normal;
  padding: 0em;
  margin: 0em;
  font-size: 1rem;
  line-height: 1.2em;
  color: rgba(0, 0, 0, 0.6);
}
/*--------------
      Icon
---------------*/
.ui.header > .icon {
  display: table-cell;
  opacity: 1;
  font-size: 1.5em;
  padding-top: 0.14285em;
  vertical-align: middle;
}
/* With Text Node */
.ui.header .icon:only-child {
  display: inline-block;
  padding: 0em;
  margin-right: 0.75rem;
}
/*-------------------
        Image
--------------------*/
.ui.header > .image,
.ui.header > img {
  display: inline-block;
  margin-top: 0.14285em;
  width: 2.5em;
  height: auto;
  vertical-align: middle;
}
.ui.header > .image:only-child,
.ui.header > img:only-child {
  margin-right: 0.75rem;
}
/*--------------
     Content
---------------*/
.ui.header .content {
  display: inline-block;
  vertical-align: top;
}
/* After Image */
.ui.header > img + .content,
.ui.header > .image + .content {
  padding-left: 0.75rem;
  vertical-align: middle;
}
/* After Icon */
.ui.header > .icon + .content {
  padding-left: 0.75rem;
  display: table-cell;
  vertical-align: middle;
}
/*--------------
 Loose Coupling
---------------*/
.ui.header .ui.label {
  font-size: '';
  margin-left: 0.5rem;
  vertical-align: middle;
}
/* Positioning */
.ui.header + p {
  margin-top: 0em;
}
/*******************************
            Types
*******************************/
/*--------------
     Page
---------------*/
h1.ui.header {
  font-size: 2rem;
}
h2.ui.header {
  font-size: 1.714rem;
}
h3.ui.header {
  font-size: 1.28rem;
}
h4.ui.header {
  font-size: 1.071rem;
}
h5.ui.header {
  font-size: 1rem;
}
/* Sub Header */
h1.ui.header .sub.header {
  font-size: 1.16666667rem;
}
h2.ui.header .sub.header {
  font-size: 1.16666667rem;
}
h3.ui.header .sub.header {
  font-size: 1rem;
}
h4.ui.header .sub.header {
  font-size: 1rem;
}
h5.ui.header .sub.header {
  font-size: 0.91666667rem;
}
/*--------------
 Content Heading
---------------*/
.ui.huge.header {
  min-height: 1em;
  font-size: 2em;
}
.ui.large.header {
  font-size: 1.714em;
}
.ui.medium.header {
  font-size: 1.28em;
}
.ui.small.header {
  font-size: 1.071em;
}
.ui.tiny.header {
  font-size: 1em;
}
/* Sub Header */
.ui.huge.header .sub.header {
  font-size: 1.16666667rem;
}
.ui.large.header .sub.header {
  font-size: 1.16666667rem;
}
.ui.header .sub.header {
  font-size: 1rem;
}
.ui.small.header .sub.header {
  font-size: 1rem;
}
.ui.tiny.header .sub.header {
  font-size: 0.91666667rem;
}
/*--------------
   Sub Heading
---------------*/
.ui.sub.header {
  padding: 0em;
  margin-bottom: 0.16666667rem;
  font-weight: bold;
  font-size: 0.83333333em;
  text-transform: uppercase;
  color: '';
}
.ui.small.sub.header {
  font-size: 0.75em;
}
.ui.sub.header {
  font-size: 0.83333333em;
}
.ui.large.sub.header {
  font-size: 0.91666667em;
}
.ui.huge.sub.header {
  font-size: 1em;
}
/*-------------------
        Icon
--------------------*/
.ui.icon.header {
  display: inline-block;
  text-align: center;
  margin: 2rem 0em 1rem;
}
.ui.icon.header:after {
  content: '';
  display: block;
  height: 0px;
  clear: both;
  visibility: hidden;
}
.ui.icon.header:first-child {
  margin-top: 0em;
}
.ui.icon.header .icon {
  float: none;
  display: block;
  width: auto;
  height: auto;
  line-height: 1;
  padding: 0em;
  font-size: 3em;
  margin: 0em auto 0.5rem;
  opacity: 1;
}
.ui.icon.header .content {
  display: block;
}
.ui.icon.header .circular.icon {
  font-size: 2em;
}
.ui.icon.header .square.icon {
  font-size: 2em;
}
.ui.block.icon.header .icon {
  margin-bottom: 0em;
}
.ui.icon.header.aligned {
  margin-left: auto;
  margin-right: auto;
  display: block;
}
/*******************************
            States
*******************************/
.ui.disabled.header {
  opacity: 0.45;
}
/*******************************
           Variations
*******************************/
/*-------------------
      Inverted
--------------------*/
.ui.inverted.header {
  color: #FFFFFF;
}
.ui.inverted.header .sub.header {
  color: rgba(255, 255, 255, 0.8);
}
.ui.inverted.attached.header {
  background: #545454 linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  box-shadow: none;
  border-color: transparent;
}
.ui.inverted.block.header {
  background: #545454 linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  box-shadow: none;
}
.ui.inverted.block.header {
  border-bottom: none;
}
